import React from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/core';
import { useTheme } from '@material-ui/styles';
import Tooltip from '@material-ui/core/Tooltip';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';
import NotInterestedIcon from '@material-ui/icons/NotInterested';

const StatusIndicator = ({ status, text }) => {
  const theme = useTheme();

  let icon = <FiberManualRecordIcon fontSize="inherit" />;

  if (status === 'error') {
    icon = <ErrorOutlineIcon fontSize="inherit" />;
  } else if (status === 'deny') {
    icon = <NotInterestedIcon fontSize="inherit" />;
  }

  if (text) {
    icon = <Tooltip title={text}>{icon}</Tooltip>;
  }

  return (
    <span
      css={css`
        display: flex;
        color: ${theme.palette[status].light};
      `}
    >
      {icon}
    </span>
  );
};

StatusIndicator.propTypes = {
  status: PropTypes.oneOf(['info', 'success', 'warning', 'error', 'deny']),
  text: PropTypes.string,
};

StatusIndicator.defaultProps = {
  status: 'info',
  text: null,
};

export default StatusIndicator;
