import React from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/core';
import { useTheme } from '@material-ui/styles';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';

import Tile from './Tile';

const ContentLink = React.forwardRef((props, ref) => {
  const { tile, icon, children, ...rest } = props;

  const theme = useTheme();

  const style = css`
    color: ${theme.palette.secondary.main};
    font-family: ${theme.typography.button.fontFamily};
    font-weight: ${theme.typography.button.fontWeight};
    line-height: ${theme.typography.button.lineHeight};
    letter-spacing: ${theme.typography.button.letterSpacing};
    text-transform: ${theme.typography.button.textTransform};
    text-align: left;
    font-size: ${16 / 16}rem;
  `;

  const inner = (
    <Box display="flex" justifyContent="left" css={style}>
      <Box mt={0.25} mr={3}>
        {icon}
      </Box>
      <Box>{children}</Box>
    </Box>
  );

  if (tile) {
    return (
      <Link ref={ref} underline="none" {...rest}>
        <Tile button>{inner}</Tile>
      </Link>
    );
  }

  return (
    <Link ref={ref} underline="none" {...rest}>
      {inner}
    </Link>
  );
});

ContentLink.propTypes = {
  tile: PropTypes.bool,
  icon: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
};

ContentLink.defaultProps = {
  tile: false,
};

export default ContentLink;
