import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/core';
import { useTheme } from '@material-ui/styles';
import Box from '@material-ui/core/Box';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Collapse from '@material-ui/core/Collapse';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';

const NavItem = ({
  icon,
  text,
  href,
  linkProps,
  active,
  highlight,
  submenuRef,
  children,
}) => {
  const theme = useTheme();
  const [expanded, setExpanded] = useState(false);

  useEffect(() => {
    submenuRef({
      href,
      expand: () => setExpanded(true),
      collapse: () => setExpanded(false),
      toggle: () => setExpanded(!!expanded),
    });
  }, []);

  return (
    <ListItem
      disableGutters
      key={href}
      css={css`
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-top: 0;
        padding-bottom: 0;
      `}
    >
      <Box display="flex" width="100%">
        <Button
          test-ref="NavItem-button"
          underline="none"
          href={href}
          {...linkProps}
          css={css`
            padding: ${theme.spacing(1.25)}px ${theme.spacing(1)}px;
            justify-content: flex-start;
            width: 100%;
            // using a 16px font here had a pretty jarring bump in size when switching to the active
            // font weight (heavy weight was a smaller font). at least on chrome. 17px looked much better.
            font-size: ${17 / 16}rem;
            color: ${active
              ? theme.palette.text.blue
              : theme.palette.text.primary};
            font-weight: ${active
              ? theme.typography.fontWeightHeavy
              : theme.typography.fontWeightMedium};

            & .MuiButton-label {
              display: flex;
              align-items: flex-start;
            }
          `}
        >
          {icon && (
            <Box
              test-ref="NavItem-icon"
              display="flex"
              color={theme.palette.icon}
              witdth={24}
              height={24}
              alignItems="center"
              mt={0.25}
              mr={3}
            >
              <Badge invisible={!highlight} color="primary" variant="dot">
                {icon}
              </Badge>
            </Box>
          )}
          <Badge invisible={icon || !highlight} color="primary" variant="dot">
            {text}
          </Badge>
        </Button>

        {children && !expanded && (
          <IconButton onClick={() => setExpanded(true)}>
            <ExpandMoreIcon />
          </IconButton>
        )}

        {children && expanded && (
          <IconButton onClick={() => setExpanded(false)}>
            <ExpandLessIcon />
          </IconButton>
        )}
      </Box>

      <Collapse
        in={children && expanded}
        css={css`
          width: 100%;
        `}
      >
        <Box ml={7}>
          <List
            css={css`
              padding: 0;
            `}
          >
            {children}
          </List>
        </Box>
      </Collapse>
    </ListItem>
  );
};

NavItem.propTypes = {
  icon: PropTypes.node,
  text: PropTypes.string.isRequired,
  href: PropTypes.string.isRequired,
  active: PropTypes.bool,
  linkProps: PropTypes.object,
  highlight: PropTypes.bool,
  submenuRef: PropTypes.func,
  children: PropTypes.node,
};

NavItem.defaultProps = {
  icon: null,
  active: false,
  linkProps: {
    component: 'a',
  },
  highlight: false,
  submenuRef: Function.prototype,
  children: null,
};

export default NavItem;
