import React from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/core';
import { useTheme } from '@material-ui/styles';

import StatusIndicator from './StatusIndicator';

const DashboardRow = ({ status, statusText, children }) => {
  const theme = useTheme();

  return (
    <div
      css={css`
        display: flex;
        border-bottom: ${theme.palette.divider} 1px solid;
        min-height: 44px;
      `}
    >
      {status && (
        <div
          css={css`
            display: flex;
            align-items: center;
            margin-right: ${theme.spacing(2)}px;
          `}
        >
          <StatusIndicator status={status} text={statusText} />
        </div>
      )}

      {children}
    </div>
  );
};

DashboardRow.propTypes = {
  status: PropTypes.oneOf(['info', 'success', 'warning', 'error', 'deny']),
  statusText: PropTypes.string,
  children: PropTypes.node.isRequired,
};

DashboardRow.defaultProps = {
  status: 'info',
  statusText: null,
};

export default DashboardRow;
