import React from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/core';
import { useTheme } from '@material-ui/styles';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import ButtonBase from '@material-ui/core/ButtonBase';
import Skeleton from '@material-ui/lab/Skeleton';

import Paragraph from './Paragraph';

const DashboardItem = ({
  button,
  statusColor,
  statusIcon,
  statusText,
  primaryText,
  secondaryText,
  rightColumn,
  renderHead,
  renderChildren,
  children,
}) => {
  const theme = useTheme();

  const head = (
    <Paper
      css={css`
        padding: ${theme.spacing(2)}px;
      `}
    >
      <Box display="flex">
        <Box flex={1}>
          <Box display="flex">
            {statusIcon ? (
              <Box
                test-ref="DashboardItem-status"
                component="span"
                display="inline-flex"
                alignItems="center"
                color={statusColor
                  .split('.')
                  .reduce((obj, key) => obj[key], theme.palette)}
                mr={1.5}
                mb={1}
              >
                {statusText ? (
                  <Tooltip title={statusText}>{statusIcon}</Tooltip>
                ) : (
                  statusIcon
                )}
              </Box>
            ) : (
              <Box mb={1} height={24} />
            )}
            <Box mr={2}>
              <Paragraph test-ref="DashboardItem-primary-text" mb={0}>
                {primaryText}
              </Paragraph>
            </Box>
          </Box>
          <Box test-ref="DashboardItem-secondary-text" color="text.hint">
            {secondaryText}
          </Box>
        </Box>
        <Box display="flex">
          <Box>{rightColumn}</Box>
        </Box>
      </Box>
    </Paper>
  );

  return (
    <div
      css={css`
        background: ${theme.palette.background.default};
        margin-bottom: ${theme.spacing(2)}px;
      `}
    >
      {button ? (
        <ButtonBase
          component="div"
          css={css`
            display: block;
            width: 100%;
            text-align: left;
          `}
        >
          {renderHead(head)}
        </ButtonBase>
      ) : (
        renderHead(head)
      )}

      {children && (
        <div
          css={css`
            padding: ${theme.spacing(2)}px;
          `}
        >
          {renderChildren(children)}
        </div>
      )}
    </div>
  );
};

DashboardItem.Skeleton = ({ showIcon }) => (
  <DashboardItem
    statusIcon={
      showIcon ? (
        <Skeleton
          component="span"
          variant="circle"
          width={24}
          height={24}
          css={css`
            margin: 0;
          `}
        />
      ) : null
    }
    primaryText={
      <Skeleton
        component="span"
        width={300}
        height={24}
        css={css`
          margin: 0;
        `}
      />
    }
    secondaryText={
      <Skeleton
        component="span"
        height={20}
        width={120}
        css={css`
          margin: 0;
        `}
      />
    }
  />
);

DashboardItem.Skeleton.propTypes = {
  showIcon: PropTypes.bool,
};

DashboardItem.Skeleton.defaultProps = {
  showIcon: true,
};

DashboardItem.propTypes = {
  button: PropTypes.bool,
  statusColor: PropTypes.string,
  statusIcon: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  statusText: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  primaryText: PropTypes.oneOfType([PropTypes.string, PropTypes.node])
    .isRequired,
  secondaryText: PropTypes.oneOfType([PropTypes.string, PropTypes.node])
    .isRequired,
  rightColumn: PropTypes.node,
  renderHead: PropTypes.func,
  renderChildren: PropTypes.func,
  children: PropTypes.node,
};

DashboardItem.defaultProps = {
  button: false,
  statusColor: 'text.primary',
  statusIcon: null,
  statusText: null,
  rightColumn: null,
  renderHead: head => head,
  renderChildren: children => children,
  children: null,
};

export default DashboardItem;
