import React from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/core';
import { useTheme } from '@material-ui/styles';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';

const NavTab = ({ icon, text, href, linkProps, active }) => {
  const theme = useTheme();

  return (
    <Button
      test-ref="NavTab-button"
      underline="none"
      href={href}
      {...linkProps}
      css={css`
        padding: ${theme.spacing(1.25)}px ${theme.spacing(4)}px;
        justify-content: flex-start;
        border-radius: 0;
        min-height: 64px;
        // using a 16px font here had a pretty jarring bump in size when switching to the active
        // font weight (heavy weight was a smaller font). at least on chrome. 17px looked much better.
        font-size: ${17 / 16}rem;
        font-weight: ${active
          ? theme.typography.fontWeightHeavy
          : theme.typography.fontWeightMedium};
        background-color: ${active
          ? theme.palette.background.secondary
          : theme.palette.white.main};

        & .MuiButton-label {
          display: flex;
          align-items: center;
        }
      `}
    >
      {active && icon && (
        <Box
          test-ref="NavTab-icon"
          display="flex"
          color={theme.palette.icon}
          witdth={32}
          height={32}
          alignItems="center"
          mt={0.25}
          mr={2}
          css={css`
            svg {
              width: 32px;
              height: 32px;
            }
          `}
        >
          {icon}
        </Box>
      )}
      {text}
    </Button>
  );
};

NavTab.propTypes = {
  icon: PropTypes.node,
  text: PropTypes.string.isRequired,
  href: PropTypes.string.isRequired,
  active: PropTypes.bool,
  linkProps: PropTypes.object,
};

NavTab.defaultProps = {
  icon: null,
  active: false,
  linkProps: {
    component: 'a',
  },
};

export default NavTab;
