import React from 'react';
import PropTypes from 'prop-types';
import { css } from '@emotion/core';
import ReactMoment from 'react-moment';
import { useTheme } from '@material-ui/styles';
import Tooltip from '@material-ui/core/Tooltip';

const Moment = ({
  tooltip,
  tooltipDateFormat,
  tooltipTimeFormat,
  children,
  ...props
}) => {
  const theme = useTheme();

  const style = tooltip
    ? css`
        cursor: default;
        border-bottom: 1px dotted ${theme.palette.text.primary};
      `
    : null;

  return (
    <span css={style}>
      {tooltip ? (
        <Tooltip
          title={
            <>
              <ReactMoment format={tooltipDateFormat}>{children}</ReactMoment>{' '}
              at{' '}
              <ReactMoment format={tooltipTimeFormat}>{children}</ReactMoment>
            </>
          }
        >
          <ReactMoment {...props}>{children}</ReactMoment>
        </Tooltip>
      ) : (
        <ReactMoment {...props}>{children}</ReactMoment>
      )}
    </span>
  );
};

Moment.propTypes = {
  tooltip: PropTypes.bool,
  tooltipDateFormat: PropTypes.string,
  tooltipTimeFormat: PropTypes.string,
  children: PropTypes.oneOfType([PropTypes.node, PropTypes.object]),
};

Moment.defaultProps = {
  tooltip: false,
  tooltipDateFormat: 'ddd, MMM D, YYYY',
  tooltipTimeFormat: 'h:mma',
  children: null,
};

export default Moment;
